
import axios from 'axios'
import { computed, defineComponent, ref, watch } from 'vue'

export const App = defineComponent({
  name: 'App',
  setup () {
    const data = ref<{name:{value:string|null;status:boolean|null};task:{value:string|null;status:boolean|null};product:{value:string|null;status:boolean|null};status:{value:string|null;status:boolean|null}}>({ name: { value: null, status: null }, task: { value: null, status: null }, product: { value: null, status: null }, status: { value: null, status: null } })
    const inputData = (type:string) => {
      switch (type) {
        case 'name':
          if (data.value.name.value === null || data.value.name.value === '') {
            data.value.name.status = false
          } else {
            data.value.name.status = true
          }
          break
        case 'product':
          if (data.value.product.value === null || data.value.product.value === '') {
            data.value.product.status = false
          } else {
            data.value.product.status = true
          }
          break
      }
    }
    const selectData = (value:string) => {
      console.log(value)
      switch (value) {
        case 'Technical Transfer':
          data.value.status.value = 'SBG Training'
          break
        case 'Alpha Test':
          data.value.status.value = 'TBC'
          break
      }
    }
    const submitTask = (type:string) => {
      //
      const timestamp = new Date()
      const dataArray = Object.values(data.value)
      const submitData = {
        name: '',
        task: '',
        product: '',
        status: '',
        type: type,
        date: timestamp.getFullYear() + '-' + String(timestamp.getMonth() + 1).padStart(2, '0') + '-' + String(timestamp.getDate()).padStart(2, '0'),
        time: String(timestamp.getHours()).padStart(2, '0') + ':' + String(timestamp.getMinutes()).padStart(2, '0')
      }
      for (let i = 0; i < dataArray.length; i++) {
        if (dataArray[i].value === null || dataArray[i].value === '') {
          return alert('請確認是否都填入')
        }
      }
      if (confirm('確認送出？')) {
        submitData.name = data.value.name.value?.trim() as string
        submitData.task = data.value.task.value as string
        submitData.product = data.value.product.value?.trim() as string
        submitData.status = data.value.status.value as string
        axios({
          method: 'POST',
          url: '/',
          data: {
            data: submitData
          }
        })
          .then((res) => {
            alert('成功送出')
          })
      }
    }
    return { data, submitTask, inputData, selectData }
  }
})
export default App
